.drop-menu {
    background-color: red !important;
}


.bloco-paciente {
    background-color: #E9F7F6;
    margin:5px;
    padding:15px;
    border-radius:12px;
    font-size: 18px;
    color: #4D4F53;
    position: relative;
}

.bloco-paciente:nth-child(2n) { background-color: #E6E6E7; }


.data_nasc {
    position: absolute;
    left:80%;top:33%;
    font-weight: bold;
    font-size: 18px;
}


.lds-ring {
    display: inline-block;
    position: relative;
    top:200px;
    left:45%;
    width: 80px;
    height: 80px;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #fff;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(209, 209, 209) transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  .position-filter {
    position: relative;
    left:10px;
  }

  .mensagem-vazio {
    position: relative;
    user-select: none;
    top:200px;

    color: rgb(211, 211, 211) !important;
  }